.NoRecord{
    opacity: 0.3;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.NoRecordFound{
    width: 100px;
    height: 100px;
    font-size: 60px;
    border: 3px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}
.NoRecord h1{
    margin-top: 10px;
}

@media only screen and (max-width: 690px){
    .NoRecord{
        /* margin-top: 100px; */
        width: 100%;
        margin: 170px 0 20px 0;
        padding-bottom: 20px;
    }
    .NoRecordFound{
        width: 70px;
        height: 70px;
        font-size: 40px;
    }
    .NoRecord h1{
        font-size: 20px;
    }
}