*{
    box-sizing: border-box;
}
.Items{
    display: flex;
    height: 100vh;
    background-color: rgb(173, 245, 209);
}
.AddItems{
    /* border: 2px solid black; */
    width: 30%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(173, 245, 209);
}
.GetItems{
    /* border: 2px solid blue; */
    width: 70%;
    height: 100vh;
}
@media only screen and (max-width: 900px){
    .Items{
        height: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .AddItems{
        /* border: 2px solid black; */
        width: 100%;
        height: 100%;
        flex-direction: column;
        background-color: rgb(173, 245, 209);
    }
    .GetItems{
        /* border: 2px solid blue; */
        width: 100%;
        height: 100%;
        background-color: rgb(173, 245, 209);
    }
}