*{
    box-sizing: border-box;
}
.AddItem{
    /* border: 1px solid rgb(0, 0, 0); */
    background-color: rgb(117, 232, 174);
    height: 95vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 15px;
}
.Logo{
    width: 150px;
    height: 55px;
    border-radius: 10px;
    background-color: rgb(40, 159, 99);
    border: 3px solid rgb(117, 232, 174);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 4px;
    margin-top: 40px;
    box-shadow: 0 0px 10px rgba(0, 0, 0, 0.4);
}
.AppIcon{
    width: 30%;
    height: 100%;
    background-color: rgb(117, 232, 174);
    display: flex;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    font-size: 25px;
}
.AppText{
    width: 70%;
    height: 100%;
    color: rgb(255, 255, 255);
    font-family: 'Stylish', sans-serif;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 35px;
}
.Title{
    font-size: 20px;
    margin-top: 10px;
    font-family: 'Stylish', sans-serif;
}
.Form{
    /* border: 1px solid rgb(0, 0, 0); */
    display: flex;
    flex-direction: column;
    width: 80%;
    margin-top: 10px;
    position: relative;
}
.Form input{
    outline: none;
    border: none;
    padding: 10px;
    margin: 10px 0;
    border-radius: 10px;
    font-size: 20px;    
    background-color: rgba(255, 255, 255, 0.7);
    font-family: 'Stylish', sans-serif;
}
.Form textarea{
    outline: none;
    border: none;
    padding: 10px;
    margin: 10px 0;
    border-radius: 10px;
    font-size: 20px;
    height: 150px;
    background-color: rgba(255, 255, 255, 0.7);
    font-family: 'Stylish', sans-serif;
}
.button{
    height: 48px;
    border: 0;
    padding: 10px;
    margin: 10px 0;
    border-radius: 10px;
    font-size: 20px;
    cursor: pointer;
    color: white;
    background-color: rgb(69, 183, 126);
    font-family: 'Stylish', sans-serif;
    transition: all 0.2s;
}
.button:hover{
    font-size: 22px;
    background-color: rgb(13, 69, 41) !important;
}
.cursor{
    position: absolute;
    top: 10px;
    left: 7px;
    font-size: 25px;
    animation: blinker 1s infinite ease-in-out;
}
@keyframes blinker {
    50% {
    opacity: 0;
    }
}
.fa-plus, .fa-xmark{
    margin-right: 5px;
}
.RemoveItemBtn{
    width: 80%;
}

@media only screen and (max-width: 900px){
    .Items{
        height: 100%;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .AddItems{
        /* border: 2px solid black; */
        width: 100%;
        height: 100%;
        flex-direction: column;
        background-color: rgb(173, 245, 209);
    }
}

@media only screen and (max-width: 900px){
    .AddItem{
        height: 100%;
        margin: 0;
        padding: 10px 0;
    }
    .Logo{
        margin-top: 20px;
    }
}