.GetItem{
    position: relative;
    margin: 15px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; 
}

.card{
    margin: 10px;
    transition: all 0.2s;
}
.GetItem .card:hover{
    color: black !important;
    background-color: white !important;
    border: 1px solid rgb(184, 184, 184);
}
.DeleteMark{
    position: absolute;
    cursor: pointer;
    font-size: 20px;
    top: 7px;
    right: 3px;
    background-color: white;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
    color: rgb(209, 5, 5);
    width: 30px;
    height: 30px;
    padding-top: 5px;
    text-align: center;
    border-radius: 50px;
    transition: name 0.3s;
}
.DeleteMark:hover{
    scale: 1.1;
}

@media only screen and (max-width: 690px){
    .GetItem{
        background-color: rgb(173, 245, 209);
        position: relative;
        margin: 15px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
    }
    .GetItem .card{
        margin: 10px;
        width: 100%;
    }
    .DeleteMark{
        font-size: 15px;
        width: 25px;
        height: 25px;
    }
    .NoRecord{
        /* margin-top: 100px; */
        width: 100%;
        margin: 170px 0 20px 0;
        padding-bottom: 20px;
    }
    .NoRecordFound{
        width: 70px;
        height: 70px;
        font-size: 40px;
    }
    .NoRecord h1{
        font-size: 20px;
    }
}